import React, { Component } from 'react';
import { Dropdown } from 'react-bootstrap';
import PerfectScrollbar from 'react-perfect-scrollbar';
import { GET_MEDIA_NAME } from '../../../../../Services/Utils/utils';
class MoveTo extends Component {

    render() {
        const { collections } = this.props
        return <Dropdown align="start" className="block-center position-relative">
                    <Dropdown.Toggle variant="" className="btn p-0 d-inline-flex align-items-center me-2 arrow_disabled" >
                        <span className="svg-icon svg-icon-4 text-gray-800">
                            <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 24 24" width="30" height="30">
                                <path fill="none" d="M0 0h24v24H0z" />
                                <path d="M12 11V8l4 4-4 4v-3H8v-2h4zm0-9c5.52 0 10 4.48 10 10s-4.48 10-10 10S2 17.52 2 12 6.48 2 12 2zm0 18c4.42 0 8-3.58 8-8s-3.58-8-8-8-8 3.58-8 8 3.58 8 8 8z" fill="currentColor" />
                            </svg>
                        </span>
                        <span className="text-gray-800 fs-7">Move to</span>
                    </Dropdown.Toggle>

                    {collections && collections.length > 0 ?
                    <Dropdown.Menu style={{ width: 230}}>
                        <PerfectScrollbar>
                            <div style={{minHeight: 180, maxHeight: 380 }}>
                        {
                            collections.map((cItem) => <div className="menu-item">
                                <Dropdown.Item>
                                    <span className="svg-icon svg-icon-4 me-1">
                                        <div className="symbol symbol-24 flex-shrink-0 me-2 bg-light">
                                            {cItem.thumbnails ? <img className="img-fluid object-center-cover" src={cItem.thumbnails} height={54} width={54} alt={GET_MEDIA_NAME(cItem.thumbnails)} /> : null}
                                        </div>
                                    </span>
                                    <span className="menu-title text-break">{cItem.name}</span>
                                </Dropdown.Item>
                            </div>
                            )
                        }
                        </div>
                        </PerfectScrollbar>
                    </Dropdown.Menu> 
                    : null}
                </Dropdown>
    }
}
export default MoveTo

